import React, {Component} from 'react';
import {GoogleLogout} from 'react-google-login';

const logout = () => {
    console.log('logout')
};

class Logout extends Component {

    render() {
        return (
            <div>
                <GoogleLogout buttonText="Logout" onLogoutSuccess={logout} />
            </div>
        );
    }
}

export default Logout;



